import React from "react" 
import {Container,Col,Row} from "react-bootstrap"
import data from  "../../../jsons/products"
import Stars from  "../stars/Stars"
import {RiArrowDropDownLine} from "react-icons/ri"
import {addToCart} from "../../../actions"
import {connect} from "react-redux"

const SingleProductComponent = (props) => {
    const [value,setValue] = React.useState(0)
    const [currentSelectedItem,setCurrentSelect] = React.useState(3)
    const currentItem = data().filter(item => item.id === props.id)
   
   const [item] = currentItem
   const  {id,img,title,stars,firstDesc,secondDesc,sizes,pieces,price,originPrice} = item
   let allSizes = ["select size:",...sizes]
   
   const handleAddToCartBtn = () => {
       
       if(parseInt(value) > 0){
           props.addToCart({img,title,firstDesc,price,pieces:value,total:parseInt(value) * parseInt(price.replace("$","")),id})
           setValue(0)
       }
       return 
   }
    return (
        <div className="single-product">
            <Container>
                <Row>
                    <Col xs="12" md="6"  >
                       <div className="img h-100" style={{backgroundImage:`url(${img})`}}></div>
                    </Col>
                    <Col className="flex-column" xs="12" md="6" >
                        <h3 className="mb-3">{title}</h3>
                        <div className="mb-4"><Stars  stars={stars} /></div>
                        <h4 className="mb-4 price"><span>{originPrice}</span>{price}</h4>
                        <p className="mb-3">{firstDesc}</p>
                        <p className="mb-3">{secondDesc}</p>
                        <select value={currentSelectedItem} className="mb-2">
                          {allSizes.map((size,ind) => {
                            
                            return <option key={ind} onClick={() => setCurrentSelect(ind)} value={ind}>{size}</option>
                        })}      
                        </select>
                        <span className="select-arrow"><RiArrowDropDownLine /></span>
                        <div className="mb-3 input d-flex" >
                            <button onClick={() => setValue(parseInt(value)+ 1)}>+</button>
                            <input type="text" value={value} onChange={(e) => setValue(e.target.value)} /> 
                            <button onClick={() => parseInt(value) > 0 ? setValue(parseInt(value)- 1) : setValue(value)}>-</button>
                        </div>
                        <p className="pieces mb-4">{pieces} pieces avalible</p>
                        <button onClick={handleAddToCartBtn} className="addToCart">Add to cart</button>
                    </Col>
                </Row>
            </Container>
        </div>
    )
   
}

const getState = (state) => {

    return {state}
}

export default connect(getState,{
    addToCart
})(SingleProductComponent)