import React from "react"
import {BsStar,BsStarHalf,BsFillStarFill} from "react-icons/bs"

const Stars  = ({stars}) => {
    let arr = [1,2,3,4,5]
    return (
        <div className="stars">
            {arr.map((num,ind) => {
                if(ind <= parseInt(stars) - 1){
                    return <div key={ind} className="star"><BsFillStarFill /></div>
                }else if(parseFloat(stars) % 1 !== 0 && parseFloat(stars)  > ind){
                    return <div key={ind} className="star"><BsStarHalf /></div>
                }else{
                    return <div key={ind} className="star"><BsStar /></div>
                }
            })}
        </div>
    )
}


export default Stars